import React from 'react';
import './App.css';
import Fundo from './back.jpg';
import Abas from './components/Abas';
import LearningPT from './components/LearningPT';
import LearningEN from './components/LearningEN';
import Console from './components/Console';
import SocialNetworkPT from './components/SocialNetworkPT';
import SocialNetworkEN from './components/SocialNetworkEN';
import AbotMeEN from './components/MeEN';
import AboutMePT from './components/MePT';
import AudioButton from './buttonclick.wav';
import AudioButtonReleased from './buttonclickrelease.wav';
import Splash from './components/Splash';

const audio = new Audio(AudioButton);

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      language: 0,
      aceito: 0,
      mount: false,
      linksPT: [
        {
          "title": "Aprendizagem",
          "open": false
        },
        {
          "title": "Console",
          "open": false
        },
        {
          "title": "Redes Sociais",
          "open": false
        },
        {
          "title": "Sobre mim",
          "open": false
        },
        {
          "title": "Change language",
          "open": false
        }
      ],
      linksEN: [
        {
          "title": "Learning",
          "open": false
        },
        {
          "title": "Console",
          "open": false
        },
        {
          "title": "Social Network",
          "open": false
        },
        {
          "title": "About me",
          "open": false
        },
        {
          "title": "Mudar de idioma",
          "open": false
        }
      ]
    }
  }
  audioPlay(){
    const audioEl = document.getElementsByClassName("audio-element-re")[0]
    audioEl.play()
  }
  audioPlayRelease(){
    const audioEl = document.getElementsByClassName("audio-element")[0]
    audioEl.play()
  }
  controlAbas(id){
    if(id !== this.state.linksPT.length - 1){
     this.audioPlayRelease()
     let items = [...this.state.linksPT];
     items[id].open = !this.state.linksPT[id].open;
     this.setState({linksPT: items});
     let itemsEN = [...this.state.linksEN];
     itemsEN[id].open = !this.state.linksEN[id].open;
     this.setState({linksEN: itemsEN});
     this.forceUpdate();
    } else{
      this.audioPlayRelease()
      if(this.state.language == 0){
        this.setState({language: 1});
      } else{
        this.setState({language: 0});
      }
    }
  }
  componentDidMount(){
    
  }
  render(){
    return <div>
      { !this.state.mount && <Splash/> }
      <div className="back">
        <div className="transparent"/>
        <img src={Fundo}/>
      </div>
      <div className="leftBar">
        { this.state.language == 0 && this.state.linksPT.map((item, index) => (
            <div onMouseDown={() => { this.audioPlay() }}
            onMouseUp={() => { this.controlAbas(index) }}>
            <Link
            key={index}
            language={this.state.language} title={item.title}/>
            </div>
        ))}
        { this.state.language == 1 && this.state.linksEN.map((item, index) => (
            <div onClick={() => { this.controlAbas(index) }}>
            <Link
            key={index}
            language={this.state.language} title={item.title}/>
            </div>
        ))}
      </div>
      { this.state.language == 0 && this.state.linksPT[0].open &&
      <Abas
      title={this.state.linksPT[0].title}
      data={LearningPT}
      />
      }
      { this.state.language == 0 && this.state.linksPT[1].open &&
      <Abas
      title={this.state.linksPT[1].title}
      data={Console}
      />
      }
      { this.state.language == 0 && this.state.linksPT[2].open &&
      <Abas
      title={this.state.linksPT[2].title}
      data={SocialNetworkPT}
      />
      }
      { this.state.language == 0 && this.state.linksPT[3].open &&
      <Abas
      title={this.state.linksPT[3].title}
      data={AboutMePT}
      />
      }
      { this.state.language == 1 && this.state.linksEN[0].open &&
      <div>
      <Abas title={this.state.linksEN[0].title}
      data={LearningEN}/>
      </div>
      }
      { this.state.language == 1 && this.state.linksEN[1].open &&
      <Abas
      title={this.state.linksEN[1].title}
      data={Console}
      />
      }
      { this.state.language == 1 && this.state.linksPT[2].open &&
      <Abas
      title={this.state.linksEN[2].title}
      data={SocialNetworkEN}
      />
      }
      { this.state.language == 1 && this.state.linksEN[3].open &&
      <Abas
      title={this.state.linksEN[3].title}
      data={AbotMeEN}
      />
      }
       <audio className="audio-element">
          <source src={AudioButton}></source>
        </audio>
        <audio className="audio-element-re">
          <source src={AudioButtonReleased}></source>
        </audio>
    </div>
  };
}

class Link extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }
    render() {
      return <div className="link">
           <li> {this.props.title} </li>
      </div>
    };
}

export default App;